import { handleActions } from 'redux-actions';
import * as actions from '../actions';
import * as bluetoothActions from '../actions/bluetooth';
import { base64ToArrayBuffer } from '../utils';

export default handleActions(
    {
        [actions.fetchRecipesSuccess](state, { payload }) {
            return {
                ...state,
                recipe: payload,
            };
        },
        [actions.fetchUserSuccess](state, { payload }) {
            return {
                ...state,
                user: payload,
            };
        },
        [actions.fetchUserDevicesSuccess](state, { payload }) {
            return {
                ...state,
                userDevices: payload,
            };
        },
        [actions.getBinaryRecipeSuccess](state, { payload }) {
            let buffer = base64ToArrayBuffer(payload.binary_recipe);
            let bufferImg = base64ToArrayBuffer(payload.binary_image_r400x400);
            return {
                ...state,
                recipeBinary: { ...payload, buffer, bufferImg },
            };
        },
        [bluetoothActions.recipeByteTransfered](state, { payload }) {
            return {
                ...state,
                byteTransfered: state.byteTransfered + payload,
            };
        },
        [bluetoothActions.imgByteTransfered](state, { payload }) {
            return {
                ...state,
                imgByteTransfered: state.imgByteTransfered + payload,
            };
        },
        [bluetoothActions.speedOfDataTransfer](state, { payload }) {
            console.log('Speed', payload);
            return {
                ...state,
                speedOfDataTransfer: isFinite(payload) ? payload : 0,
            };
        },
        [actions.saveDeviceSuccess](state, { payload }) {
            return {
                ...state,
                userDevices: payload,
            };
        },
        [actions.removeDeviceSuccess](state, { payload }) {
            return {
                ...state,
                userDevices: payload,
            };
        },
        [bluetoothActions.fetchLogsSuccess](state, { payload }) {
            return {
                ...state,
                logs: Object.assign(
                    {},
                    { ...state.logs },
                    { status: 'success', data: payload }
                ),
            };
        },
        [bluetoothActions.fetchLogsFailure](state) {
            return {
                ...state,
                logs: Object.assign(
                    {},
                    { ...state.logs },
                    { status: 'failure' }
                ),
            };
        },
        [bluetoothActions.fetchLogsRequest](state) {
            return {
                ...state,
                logs: Object.assign(
                    {},
                    { ...state.logs },
                    { status: 'request' }
                ),
            };
        },
    },
    {
        recipe: [],
        user: null,
        userDevices: null,
        recipeBinary: null,
        byteTransfered: 0,
        imgByteTransfered: 0,
        speedOfDataTransfer: 0,
        logs: { status: 'success', data: null },
    }
);
