class ActionUtils {
    prepareControlReceiveBuffer = (action, data) => {
        if (action === 'writeRecipeRequest' && data.length) {
            let buffer = new ArrayBuffer(20);
            let msgKeyWord8 = 0x0a;
            let operationCode = 0xff;
            // let dataSize32 = 0x2334;
            let dataSize32 = data.length;
            let dataType16 = 0x6;

            new DataView(buffer).setInt8(0, msgKeyWord8, true);
            new DataView(buffer).setInt8(1, operationCode, true);
            new DataView(buffer).setInt32(2, dataSize32, true);
            new DataView(buffer).setInt16(6, dataType16, true);
            return buffer;
        }
        if (action === 'writeImgRequest' && data.length) {
            let buffer = new ArrayBuffer(20);
            let msgKeyWord8 = 0x0a;
            let operationCode = 0xff;
            // let dataSize32 = 0x2334;
            let dataSize32 = data.length;
            let dataType16 = 0x2;

            new DataView(buffer).setInt8(0, msgKeyWord8, true);
            new DataView(buffer).setInt8(1, operationCode, true);
            new DataView(buffer).setInt32(2, dataSize32, true);
            new DataView(buffer).setInt16(6, dataType16, true);
            return buffer;
        }
        if (action === 'fetchLogs') {
            let buffer = new ArrayBuffer(20);
            let msgKeyWord8 = 0x0e;
            let operationCode = 0x00;
            // let dataSize32 = 0x2334;
            let dataSize32 = 0;
            let dataType16 = 0x7;

            new DataView(buffer).setInt8(0, msgKeyWord8, true);
            new DataView(buffer).setInt8(1, operationCode, true);
            new DataView(buffer).setInt32(2, dataSize32, true);
            new DataView(buffer).setInt16(6, dataType16, true);
            return buffer;
        }
        if (action === 'fetchLogsReady') {
            let buffer = new ArrayBuffer(20);
            let msgKeyWord8 = 0x0a;
            let operationCode = 0x00;
            // let dataSize32 = 0x2334;
            let dataSize32 = 0;
            let dataType16 = 0x7;

            new DataView(buffer).setInt8(0, msgKeyWord8, true);
            new DataView(buffer).setInt8(1, operationCode, true);
            new DataView(buffer).setInt32(2, dataSize32, true);
            new DataView(buffer).setInt16(6, dataType16, true);
            return buffer;
        }
    };
}

const instance = new ActionUtils();
export default instance;
