import React, { Component } from 'react';
import { Card, Button, Divider, Progress, Modal, Col } from 'antd';
import {
    // PlusCircleOutlined,
    CloseCircleOutlined,
    CheckCircleTwoTone,
    LoadingOutlined,
} from '@ant-design/icons';

class Device extends Component {
    componentDidUpdate = (prevProps) => {
        const { connectedDevice } = this.props;
        if (
            connectedDevice &&
            connectedDevice.GATTDisconnected &&
            prevProps.connectedDevice &&
            prevProps.connectedDevice.GATTDisconnected
        ) {
            console.log('componentDidUpdate');
            Modal.error({
                title: 'Failed to reconnect',
                content: 'Please try again later',
            });
        }
    };

    render() {
        const {
            device,
            connected,
            connectToDevice,
            removeDevice,
            batteryService,
            showDrawer,
            byteTransfered,
            imgByteTransfered,
            recipeBinary,
            server,
            GATTConnected,
            // disconnectGattServer,
            // connectedDevice,
            connectionRequest,
            speedOfDataTransfer,
            logs,
        } = this.props;

        let deviceState = '';

        if (GATTConnected) {
            deviceState = 'online';
        } else {
            if (connectionRequest) {
                deviceState = 'connecting...';
            } else {
                deviceState = 'offline';
            }
        }

        let logsResult = null;
        switch (logs.status) {
            case 'success':
                logsResult = <CheckCircleTwoTone twoToneColor="#52c41a" />;
                break;
            case 'request':
                logsResult = <LoadingOutlined twoToneColor="#52c41a" />;
                break;
            case 'failure':
                logsResult = <CloseCircleOutlined twoToneColor="#52c41a" />;
                break;
            default:
                break;
        }

        if (!connected) {
            return (
                <Col>
                    <Card
                        title={
                            <>
                                <Divider type="vertical" />
                                <span>{device.name}</span>
                            </>
                        }
                        extra={
                            <div>
                                <Button
                                    style={{ color: 'red' }}
                                    shape="circle"
                                    icon={
                                        <CloseCircleOutlined twoToneColor="#52c41a" />
                                    }
                                    onClick={() => removeDevice(device.uid)}
                                />
                            </div>
                        }
                        style={{ width: 300 }}
                    >
                        <p>
                            <strong>Device: </strong>
                            {deviceState}
                        </p>
                        {/* <p>
                            <strong>UID:</strong> {device.uid}
                        </p> */}
                        <br />
                        <Button
                            // shape="circle"
                            type="primary"
                            // icon={<PlusCircleOutlined />}
                            onClick={() => {
                                // alert('button connect');
                                connectToDevice([{ name: device.name }]);
                            }}
                        >
                            Connect
                        </Button>
                    </Card>
                </Col>
            );
        }

        return (
            <Col>
                <Card
                    title={
                        <>
                            {GATTConnected && (
                                <CheckCircleTwoTone twoToneColor="#52c41a" />
                            )}
                            {connectionRequest && (
                                <LoadingOutlined twoToneColor="#52c41a" />
                            )}
                            <Divider type="vertical" />
                            <span>{device.name}</span>
                        </>
                    }
                    extra={
                        <div>
                            <Button
                                // type="primary"
                                style={{ color: 'red' }}
                                shape="circle"
                                icon={<CloseCircleOutlined />}
                                onClick={() => removeDevice(device.uid)}
                            />
                        </div>
                    }
                    style={{ width: 300 }}
                >
                    <p>
                        <strong>Device: </strong>
                        {deviceState}
                    </p>
                    {/* <p>
                    {GATTConnected && (
                        <button
                            onClick={() =>
                                disconnectGattServer(connectedDevice)
                            }
                        >
                            
                        </button>
                    )}
                </p> */}
                    <p>
                        <strong>UID:</strong> {device.uid}
                    </p>
                    <p>
                        <strong>Logs:</strong> {logsResult}
                    </p>
                    {recipeBinary && (
                        <p>
                            <strong>Bytes transferred:</strong>{' '}
                            {`${byteTransfered + imgByteTransfered}/${
                                recipeBinary.buffer.byteLength +
                                recipeBinary.bufferImg.byteLength
                            }`}
                        </p>
                    )}

                    {recipeBinary && (
                        <p>
                            <strong>Speed:</strong>{' '}
                            {'kB/s: ' + (speedOfDataTransfer / 1024).toFixed(2)}
                        </p>
                    )}

                    {batteryService &&
                    Number.isInteger(batteryService.level) ? (
                        <div>
                            <strong>Battery level: </strong>
                            <Progress
                                percent={batteryService.level}
                                // percent={45}
                                size="small"
                                status="normal"
                            />
                        </div>
                    ) : null}

                    <br />

                    {server && (
                        <Button onClick={showDrawer}>
                            Add recipe to device
                        </Button>
                    )}
                    {/* {recipeBinary ? (
                        <Progress
                            type="circle"
                            percent={Math.floor(
                                ((byteTransfered + imgByteTransfered) * 100) /
                                    (recipeBinary.buffer.byteLength +
                                        recipeBinary.bufferImg.byteLength)
                            )}
                            width={40}
                        />
                    ) : (
                        ''
                    )} */}
                </Card>
            </Col>
        );
    }
}

export default Device;
