export const onDisconnected = (event) => {
    return (dispatch) => {
        dispatch(deviceDisconnected());

        const device = event.target;

        console.log(`Device ${device.name} is disconnected.`);
        // console.log(event);

        dispatch(connectToGATTServer(device));
    };
};

export const connectGattRequest = createAction('CONNECT_GATT_REQUEST');
export const connectGattSuccess = createAction('CONNECT_GATT_SUCCESS');
export const connectGattFailure = createAction('CONNECT_GATT_FAILURE');

//   GATT   
export const connectToGATTServer = (device) => {
    return (dispatch) => {
        // dispatch(connectGattRequest());
        // device.gatt
        //     .connect()
        //     .then((server) => {
        //         console.log(
        //             '%cGATT server:',
        //             'color:green; font-size:2em; border-top:10px solid red'
        //         );
        //         console.log(server);

        //         dispatch(connectGattSuccess({ server, device }));
        //     })
        //     .catch(() => {
        //         console.error('connectGattFailure');
        //         dispatch(connectGattFailure());
        //     });

        connect();

        function connect() {
            exponentialBackoff(
                3 /* max retries */,
                2 /* seconds delay */,
                function toTry() {
                    dispatch(connectGattRequest({ device }));
                    time('Connecting to Bluetooth Device... ');
                    return device.gatt.connect();
                },
                function success(server) {
                    console.log(
                        '%cGATT server:',
                        'color:green; font-size:2em; border-top:10px solid red'
                    );
                    console.log(server);

                    dispatch(connectGattSuccess({ server, device }));

                    //    .   

                    try {
                        //   
                        // dispatch(batteryService());

                        //    
                        // dispatch(getLogs());

                        //    
                        dispatch(recipeInfo());
                    } catch (e) {
                        console.log('catch from success GATT', e.message);
                    }
                },
                function fail() {
                    dispatch(connectGattFailure({ device }));
                    time('Failed to connect.');
                }
            );
        }

        function exponentialBackoff(max, delay, toTry, success, fail) {
            toTry()
                .then((server) => success(server))
                .catch((error) => {
                    console.log('catch', error);
                    if (max === 0) {
                        return fail();
                    }
                    time(
                        'Retrying in ' + delay + 's... (' + max + ' tries left)'
                    );
                    setTimeout(function () {
                        exponentialBackoff(
                            --max,
                            delay * 2,
                            toTry,
                            success,
                            fail
                        );
                    }, delay * 1000);
                });
        }

        function time(text) {
            console.log('[' + new Date().toJSON().substr(11, 8) + '] ' + text);
        }
    };
};

export const disconnectGattServer = (device) => {
    return () => {
        // console.log('disconnectGattServer', device);
        device.device.gatt.disconnect();
    };
};

export const fetchLogsRequest = createAction('FETCH_LOGS_REQUEST');
export const fetchLogsSuccess = createAction('FETCH_LOGS_SUCCESS');
export const fetchLogsFailure = createAction('FETCH_LOGS_FAILURE');

export const getLogs = () => {
    return (dispatch, getState) => {
        dispatch(fetchLogsRequest());

        const {
            bluetooth: { server, device },
        } = getState();

        let service = null;

        let logs = null;
        let key = 0;

        server
            .getPrimaryService(dataTransferService.uuid)
            .then((ser) => {
                // console.log('1');
                service = ser;
                return service.getCharacteristic(
                    dataTransferService.characteristics.controlTransmit.uuid
                );
            })
            .then((characteristic) => {
                return new Promise((resolve) => {
                    setTimeout(() => {
                        resolve(characteristic);
                    }, 1000);
                });
            })
            .then((characteristic) => {
                // console.log('2');
                return characteristic.startNotifications();
            })
            .then((characteristic) => {
                // console.log('3');
                characteristic.oncharacteristicvaluechanged = handleControlReceive;

                console.log('  Control Receive', characteristic);

                return service.getCharacteristic(
                    dataTransferService.characteristics.controlReceive.uuid
                );
            })
            .then((characteristic) => {
                let buffer = Utils.prepareControlReceiveBuffer('fetchLogs');

                console.log(
                    '%ccontrol receive:',
                    'color:green; font-size:2em; border-top:10px solid red'
                );
                console.log(new Int8Array(buffer));
                characteristic.writeValue(new Int8Array(buffer));
            })
            .catch((error) => {
                console.log('no logs', error);
                dispatch(fetchLogsFailure());
            });

        function handleStatusTransferData(event) {
            let value = event.target.value;
            console.log('handleStatusTransferData', value, value.getUint8(0));
        }

        function handleTransmitData(event) {
            let value = event.target.value;
            console.log(
                'handleTransmitData',
                value.buffer,
                value.getUint8(0),
                'logs byteLength: ' + logs.byteLength,
                'logs buffer: ' + logs
            );

            let i = 0;
            while (i < 20) {
                if (key >= logs.byteLength) {
                    break;
                }
                // console.log('Uint8', value.getUint8(i));
                new DataView(logs).setInt8(key, value.getUint8(i));
                key++;
                i++;
            }
            // console.log(logs);

            if (key === logs.byteLength) {
                let base64String = btoa(
                    String.fromCharCode(...new Uint8Array(logs))
                );
                console.log('base64', base64String);
                dispatch(fetchLogsSuccess(base64String));
                dispatch(addHistory(base64String, device.name));
            }
        }

        function handleControlReceive(event) {
            let status = event.target.value.getUint8(0),
                operationCode = event.target.value.getUint8(1),
                dataLength = event.target.value.getUint32(2, true),
                dataType = event.target.value.getUint16(6, true);
            console.log(
                'handleControlReceive',
                status,
                '----',
                operationCode,
                '----',
                dataType,
                '----',
                event.target.value
            );

            if (0x0a === status && dataType === 0x7 && dataLength) {
                logs = new ArrayBuffer(dataLength);

                service
                    .getCharacteristic(
                        dataTransferService.characteristics.status.uuid
                    )
                    .then((characteristic) => {
                        return characteristic.startNotifications();
                    })
                    .then((characteristic) => {
                        characteristic.oncharacteristicvaluechanged = handleStatusTransferData;
                        return service.getCharacteristic(
                            dataTransferService.characteristics.dataTransmit
                                .uuid
                        );
                    })
                    .then((characteristic) => {
                        return characteristic.startNotifications();
                    })
                    .then((characteristic) => {
                        characteristic.oncharacteristicvaluechanged = handleTransmitData;

                        return service.getCharacteristic(
                            dataTransferService.characteristics.controlReceive
                                .uuid
                        );
                    })
                    .then((characteristic) => {
                        let buffer = Utils.prepareControlReceiveBuffer(
                            'fetchLogsReady'
                        );

                        console.log(
                            '%ccontrol receive:',
                            'color:green; font-size:2em; border-top:10px solid red'
                        );
                        console.log(new Int8Array(buffer));
                        characteristic.writeValue(new Int8Array(buffer));
                    })
                    .catch((error) => {
                        alert('prepare to sending data error: ' + error);
                    });
            }
        }
    };
};

export const fetchBatteryServiceRequest = createAction(
    'FETCH_BATTERY_SERVICE_REQUEST'
);
export const fetchBatteryServiceSuccess = createAction(
    'FETCH_BATTERY_SERVICE_SUCCESS'
);
export const fetchBatteryServiceFailure = createAction(
    'FETCH_BATTERY_SERVICE_FAILURE'
);

export const fetchBatteryCharacteristicSuccess = createAction(
    'FETCH_BATTERY_CHARACTERISTIC_SUCCESS'
);

export const batteryService = () => {
    return (dispatch, getState) => {
        dispatch(fetchBatteryServiceRequest());

        const {
            bluetooth: { server },
        } = getState();
        // console.log('1');

        let service = null;
        server
            .getPrimaryService('battery_service')
            .then((ser) => {
                service = ser;
                dispatch(fetchBatteryServiceSuccess());
                // console.log('2');
                return service.getCharacteristic('battery_level');
            })
            .catch(() => {
                // console.log('3');
                dispatch(fetchBatteryServiceFailure());
            })
            .then((characteristic) => {
                return characteristic.startNotifications();
            })
            .then((characteristic) => {
                // console.log('4');
                // if (characteristic) {
                //     console.log('4.1');
                //     characteristic.startNotifications();
                //     characteristic.addEventListener(
                //         'characteristicvaluechanged',
                //         handleBatteryLevelChange
                //     );
                //     return characteristic.readValue();
                // }
                // throw new Error('characteristic baterry level not found');

                characteristic.addEventListener(
                    'characteristicvaluechanged',
                    handleBatteryLevelChange
                );

                return characteristic.readValue();
            })
            .then((value) => {
                // console.log('Battery service', value.buffer);
                // console.log('5');
                dispatch(
                    fetchBatteryServiceSuccess({ level: value.getUint8(0) })
                );
            })
            .catch(() => {});

        function handleBatteryLevelChange(event) {
            dispatch(
                fetchBatteryServiceSuccess({
                    level: event.target.value.getUint8(0),
                })
            );
        }
    };
};

export const fetchDeviceInfoRequest = createAction('FETCH_DEVICE_INFO_REQUEST');
export const fetchDeviceInfoSuccess = createAction('FETCH_DEVICE_INFO_SUCCESS');
export const fetchDeviceInfoFailure = createAction('FETCH_DEVICE_INFO_FAILURE');

export const fetchDeviceInfoCharacteristicSuccess = createAction(
    'FETCH_DEVICE_INFO_CHARACTERISTIC_SUCCESS'
);

export const deviceInfoService = () => {
    return (dispatch, getState) => {
        dispatch(fetchDeviceInfoRequest());

        const {
            bluetooth: { server },
        } = getState();

        server
            .getPrimaryService(0x180a)
            .then((service) => {
                dispatch(fetchDeviceInfoSuccess());
                return service.getCharacteristic(0x2a29);
            })
            .catch(() => {
                dispatch(fetchDeviceInfoFailure());
            })
            .then((characteristic) => {
                if (characteristic) {
                    return characteristic.readValue();
                }
                throw new Error('characteristic manufacturerName not found');
            })
            .then((value) => {
                dispatch(
                    fetchDeviceInfoCharacteristicSuccess({
                        manufacturerName: value.getUint8(0),
                    })
                );
            })
            .catch(() => {});
    };
};

