export const writeRecipeToDeviceRequest = createAction(
    'WRITE_RECIPE_TO_DEVICE_REQUEST'
);
export const writeRecipeToDeviceSuccess = createAction(
    'WRITE_RECIPE_TO_DEVICE_SUCCESS'
);
export const writeRecipeToDeviceFailure = createAction(
    'WRITE_RECIPE_TO_DEVICE_FAILURE'
);

export const recipeByteTransfered = createAction('RECIPE_BYTE_TRANSFERED');
export const imgByteTransfered = createAction('IMG_BYTE_TRANSFERED');
export const speedOfDataTransfer = createAction('SPEED_OF_DATA_TRANSFER');

let keyI = 0;
let characteristicWrite = null;

export const writeRecipeToDevice = () => {
    return (dispatch, getState) => {
        const {
            bluetooth: { server },
            data: { recipeBinary },
        } = getState();

        let service = null;

        // connect to service

        // dispatch(showAppPreloader(true));

        server
            .getPrimaryService(dataTransferService.uuid)
            .then((ser) => {
                service = ser;
                return service.getCharacteristic(
                    dataTransferService.characteristics.controlTransmit.uuid
                );
            })
            .then((characteristic) => {
                return new Promise((resolve) => {
                    setTimeout(() => {
                        resolve(characteristic);
                    }, 1000);
                });
            })
            // .then((characteristic) => {
            //     return characteristic.stopNotifications();
            // })
            .then((characteristic) => {
                return characteristic.startNotifications();
            })
            .then((characteristic) => {
                characteristic.oncharacteristicvaluechanged = handleControlReceive;

                console.log('  Control Transmit', characteristic);
                return service.getCharacteristic(
                    dataTransferService.characteristics.controlReceive.uuid
                );
            })
            .then((characteristic) => {
                let buffer = Utils.prepareControlReceiveBuffer(
                    'writeRecipeRequest',
                    {
                        length: recipeBinary.buffer.byteLength,
                    }
                );

                console.log(
                    '%ccontrol receive:',
                    'color:green; font-size:2em; border-top:10px solid red'
                );
                console.log(new Int8Array(buffer));
                setTimeout(() => {
                    characteristic.writeValue(new Int8Array(buffer));
                }, 1000);
            })
            .catch((error) => {
                dispatch(showAppPreloader(false));
                alert(error);
            });

        function handleControlReceive(event) {
            let status = event.target.value.getUint8(0),
                operationCode = event.target.value.getUint8(1),
                // dataLength = event.target.value.getUint32(2, true),
                dataType = event.target.value.getUint16(6, true);
            console.log(
                'handleControlReceive',
                status,
                '----',
                operationCode,
                '----',
                dataType,
                '----',
                event.target.value
            );
            if (0x0a === status && dataType === 0x6 && operationCode === 0xff) {
                console.log('prepare to sending data...');
                service
                    .getCharacteristic(
                        dataTransferService.characteristics.status.uuid
                    )
                    .then((characteristic) => {
                        return characteristic.startNotifications();
                    })
                    .then((characteristic) => {
                        characteristic.oncharacteristicvaluechanged = handleStatusTransferData;
                        console.log('prepare to sending data 2...');
                        return service.getCharacteristic(
                            dataTransferService.characteristics.dataReceive.uuid
                        );
                    })
                    .then((characteristic) => {
                        characteristicWrite = characteristic;
                        console.log('prepare to sending data 3...');
                        setTimeout(() => sendingData(), 2000);
                        // sendingData();
                    })
                    .catch((error) => {
                        dispatch(showAppPreloader(false));
                        alert('prepare to sending data error: ' + error);
                    });
            } else if (
                0x0a === status &&
                dataType === 0x2 &&
                operationCode === 0xff
            ) {
                //  
                setTimeout(
                    () =>
                        service
                            .getCharacteristic(
                                dataTransferService.characteristics.status.uuid
                            )
                            .then((characteristic) => {
                                return characteristic.startNotifications();
                            })
                            .then((characteristic) => {
                                characteristic.oncharacteristicvaluechanged = handleStatusTransferDataImg;

                                return service.getCharacteristic(
                                    dataTransferService.characteristics
                                        .dataReceive.uuid
                                );
                            })
                            .then((characteristic) => {
                                characteristicWrite = characteristic;

                                sendingDataImg();
                            }),
                    2000
                );
            } else if (0x0f === status && dataType === 0x6) {
                console.log('Recipe did saved');
                dispatch(showAppPreloader(false));
                setTimeout(() => {
                    // dispatch(showAppPreloader(true));
                    service
                        .getCharacteristic(
                            dataTransferService.characteristics.controlReceive
                                .uuid
                        )
                        .then((characteristic) => {
                            let buffer = Utils.prepareControlReceiveBuffer(
                                'writeImgRequest',
                                {
                                    length: recipeBinary.bufferImg.byteLength,
                                }
                            );

                            console.log(
                                '%ccontrol receive:',
                                'color:green; font-size:2em; border-top:10px solid red'
                            );
                            console.log(
                                new Int8Array(buffer),
                                recipeBinary.bufferImg.byteLength
                            );
                            characteristic.writeValue(new Int8Array(buffer));
                        })
                        .catch((error) => {
                            dispatch(showAppPreloader(false));
                            alert(error);
                        });
                }, 2000);
            } else if (0x0f === status && dataType === 0x2) {
                console.log('Img did saved');
                dispatch(showAppPreloader(false));
            } else if (0x0d === status) {
                console.warn("Don't have space");
            }

            console.log(
                '%ccontrol transmit:',
                'color:green; font-size:2em; border-top:10px solid red'
            );

            switch (status) {
                case 0x0a:
                    console.log(
                        '%cI am ready',
                        'color:black; font-size:1em; border: 1px dashed gray;'
                    );
                    keyI = 0;
                    break;
                case 0x0b:
                    console.log(
                        '%cPaused',
                        'color:black; font-size:1em; border: 1px dashed gray;'
                    );
                    break;
                case 0x0c:
                    console.log(
                        '%cAborted',
                        'color:black; font-size:1em; border: 1px dashed gray;'
                    );
                    break;
                case 0x0d:
                    console.log(
                        '%cDont`t have space',
                        'color:black; font-size:1em; border: 1px dashed gray;'
                    );
                    break;
                case 0x0e:
                    console.log(
                        '%cBusy',
                        'color:black; font-size:1em; border: 1px dashed gray;'
                    );
                    break;
                case 0x0f:
                    console.log(
                        '%cSuccess',
                        'color:black; font-size:1em; border: 1px dashed gray;'
                    );
                    break;
                default:
                    console.log(
                        '%cUndefined status: ' + status,
                        'color:black; font-size:1em; border: 1px dashed gray;'
                    );
                    break;
            }
        }

        function handleStatusTransferData(event) {
            let value = event.target.value;
            console.log('handleStatusTransferData', value, value.getUint8(0));
            sendingData();
        }

        function handleStatusTransferDataImg(event) {
            let value = event.target.value;
            console.log(
                'handleStatusTransferDataImg',
                value,
                value.getUint8(0)
            );
            sendingDataImg();
        }

        async function sendingData() {
            console.log('sendingData...');
            let startTransfer = Date.now() / 1000;

            for (
                let sum = 0;
                keyI < recipeBinary.buffer.byteLength && sum < 400;
                keyI = keyI + 20, sum = sum + 20
            ) {
                try {
                    console.log(
                        'recipeBinary await',
                        recipeBinary.buffer.slice(keyI, keyI + 20)
                    );
                    await characteristicWrite.writeValue(
                        recipeBinary.buffer.slice(keyI, keyI + 20)
                    );
                } catch (e) {
                    console.log('catch', e);
                }
                dispatch(
                    recipeByteTransfered(
                        recipeBinary.buffer.slice(keyI, keyI + 20).byteLength
                    )
                );
                console.log(
                    'DataBuffer',
                    recipeBinary.buffer.slice(keyI, keyI + 20)
                );
            }

            let endTransfer = Date.now() / 1000;

            dispatch(speedOfDataTransfer(400 / (endTransfer - startTransfer)));
        }

        async function sendingDataImg() {
            console.log('sendingData...');
            let startTransfer = Date.now() / 1000;

            for (
                let sum = 0;
                keyI < recipeBinary.bufferImg.byteLength && sum < 400;
                keyI = keyI + 20, sum = sum + 20
            ) {
                await characteristicWrite.writeValue(
                    recipeBinary.bufferImg.slice(keyI, keyI + 20)
                );
                dispatch(
                    imgByteTransfered(
                        recipeBinary.bufferImg.slice(keyI, keyI + 20).byteLength
                    )
                );
                console.log(
                    'DataBuffer',
                    recipeBinary.bufferImg.slice(keyI, keyI + 20)
                );
            }

            let endTransfer = Date.now() / 1000;

            dispatch(speedOfDataTransfer(400 / (endTransfer - startTransfer)));
        }
    };
};
