export const logoutRequest = createAction('LOGOUT_REQUEST');
export const logoutSuccess = createAction('LOGOUT_SUCCESS');
export const logoutFailure = createAction('LOGOUT_FAILURE');

export const logout = () => {
    return (dispatch) => {
        dispatch(logoutRequest());
        try {
            removeStateFromCookie('auth_cookie');
            dispatch(logoutSuccess());
        } catch (e) {
            dispatch(logoutFailure());
            console.error('logout', e.message);
        }
    };
};

///////////////////////
////// recipes ///////
/////////////////////

export const fetchRecipesRequest = createAction('FETCH_RECIPES_REQUEST');
export const fetchRecipesSuccess = createAction('FETCH_RECIPES_SUCCESS');
export const fetchRecipesFailure = createAction('FETCH_RECIPES_FAILURE');

export const recipe = () => {
    return (dispatch, getState) => {
        dispatch(fetchRecipesRequest());
        dispatch(showAppPreloader(true));

        const {
            auth: {
                cookie: { access_token },
            },
        } = getState();

        axios
            .get('recipe/', {
                headers: {
                    Authorization: `Bearer ${access_token}`,
                },
            })
            .then((response) => {
                dispatch(fetchRecipesSuccess(response.data));
                dispatch(showAppPreloader(false));
            })
            .catch(() => {
                dispatch(fetchRecipesFailure());
                dispatch(showAppPreloader(false));
            });
    };
};

