export const fetchUserRequest = createAction('FETCH_USER_REQUEST');
export const fetchUserSuccess = createAction('FETCH_USER_SUCCESS');
export const fetchUserFailure = createAction('FETCH_USER_FAILURE');

export const user = () => {
    return (dispatch, getState) => {
        dispatch(fetchRecipesRequest());
        dispatch(showAppPreloader(true));
        const {
            auth: {
                cookie: { access_token },
            },
        } = getState();
        axios
            .get('user/', {
                headers: {
                    Authorization: `Bearer ${access_token}`,
                },
            })
            .then((response) => {
                dispatch(fetchUserSuccess(response.data));
                dispatch(showAppPreloader(false));
            })
            .catch((e) => {
                dispatch(fetchUserFailure());
                dispatch(showAppPreloader(false));

                switch (e.response.status) {
                    case 403:
                        navigate('/');
                        break;
                    default:
                        break;
                }
            });
    };
};

