export const postLogsRequest = createAction('POST_LOGS_REQUEST');
export const postLogsSuccess = createAction('POST_LOGS_SUCCESS');
export const postLogsFailure = createAction('POST_LOGS_FAILURE');

export const addHistory = (base64String, deviceUID) => {
    return (dispatch, getState) => {
        const {
            auth: {
                cookie: { access_token },
            },
        } = getState();

        // console.log('addHistory');

        dispatch(postLogsRequest());
        dispatch(showAppPreloader(true));

        axios
            .post(
                '/history_usage/add_history/',
                {
                    history_string: base64String,
                    device_uid: deviceUID,
                },
                {
                    headers: {
                        Authorization: `Bearer ${access_token}`,
                    },
                }
            )
            .then((response) => {
                dispatch(postLogsSuccess(response.data));
                dispatch(showAppPreloader(false));
            })
            .catch(() => {
                dispatch(postLogsFailure());
                dispatch(showAppPreloader(false));
            });
    };
};
