import { handleActions } from 'redux-actions';
// import * as actions from '../actions';
import * as bluetoothActions from '../actions/bluetooth';
// import _ from 'lodash';

export default handleActions(
    {
        [bluetoothActions.connectToDeviceSuccess](state, { payload }) {
            let newDevices = state.devices.slice();
            newDevices.push({
                id: payload.id,
                name: payload.name,
                device: payload,
            });
            return {
                ...state,
                device: payload,
                devices: newDevices,
            };
        },
        [bluetoothActions.connectGattRequest](state, { payload }) {
            console.log('connectGattRequest', payload);
            let devices = state.devices.map((device) => {
                if (device.name === payload.device.name) {
                    device.connectionRequest = true;
                }
                return device;
            });
            return {
                ...state,
                server: payload.server,
                devices,
            };
        },
        [bluetoothActions.connectGattSuccess](state, { payload }) {
            let devices = state.devices.map((device) => {
                if (device.name === payload.device.name) {
                    device.connectionRequest = false;
                    device.server = payload.server;
                    device.GATTDisconnected = false;
                }
                return device;
            });
            return {
                ...state,
                server: payload.server,
                devices,
            };
        },
        [bluetoothActions.connectGattFailure](state, { payload }) {
            let devices = state.devices.map((device) => {
                if (device.name === payload.device.name) {
                    device.GATTDisconnected = true;
                    device.connectionRequest = false;
                }
                return device;
            });
            return {
                ...state,
                devices,
            };
        },
        [bluetoothActions.fetchBatteryServiceSuccess](state, { payload }) {
            const { batteryService } = state;
            return {
                ...state,
                batteryService: { ...batteryService, ...payload },
            };
        },
        [bluetoothActions.fetchDeviceInfoCharacteristicSuccess](
            state,
            { payload }
        ) {
            const { deviceInfoService } = state;
            return {
                ...state,
                deviceInfoService: { ...deviceInfoService, ...payload },
            };
        },
    },
    {
        device: null,
        devices: [],
        server: null,
        batteryService: null,
        deviceInfoService: null,
    }
);
